// Cloudflare Pages Function - AI Chatbot API
// Uses Groq API for LLM responses (compatible with Cloudflare Workers)

// System prompt for Hero Ai
const SYSTEM_PROMPT = `You are "Hero Ai", an expert financial assistant specializing in the Saudi Arabian stock market (TASI/Tadawul), Egyptian stock market (EGX), and global markets.

Your expertise includes:
- Stock analysis (fundamental & technical)
- Market trends and predictions
- Investment strategies and portfolio advice
- Company financial analysis
- Trading recommendations
- Risk assessment

Guidelines:
1. Be concise but informative (2-4 paragraphs max)
2. When stock data is provided, reference specific numbers
3. Always mention risks with recommendations
4. Be helpful and encouraging to investors
5. Use emojis sparingly for key points (📈 📉 💡 ⚠️ 🎯)
6. Format responses clearly with **bold** for emphasis
7. If you don't have specific data, provide general guidance
8. Never give guaranteed predictions - markets are unpredictable

Key Saudi Stocks:
- 2222 (Aramco), 1120 (Al Rajhi), 2010 (SABIC), 7010 (STC), 2082 (ACWA Power)

Respond in a friendly, professional manner as a trusted financial advisor named Hero Ai.`;

// Stock symbol detection
function extractStockSymbols(message) {
    const symbols = [];
    const msg = message.toLowerCase();

    if (msg.includes('aramco') || msg.includes('2222')) symbols.push('2222.SR');
    if (msg.includes('rajhi') || msg.includes('1120')) symbols.push('1120.SR');
    if (msg.includes('sabic') || msg.includes('2010')) symbols.push('2010.SR');
    if (msg.includes('stc') || msg.includes('7010')) symbols.push('7010.SR');
    if (msg.includes('acwa') || msg.includes('2082')) symbols.push('2082.SR');
    if (msg.includes('apple') || msg.includes('aapl')) symbols.push('AAPL');
    if (msg.includes('tesla') || msg.includes('tsla')) symbols.push('TSLA');
    if (msg.includes('nvidia') || msg.includes('nvda')) symbols.push('NVDA');

    return symbols;
}

// Helper: Create JSON response
const jsonResponse = (data, status = 200) => {
    return new Response(JSON.stringify(data), {
        status,
        headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
            'Access-Control-Allow-Headers': 'Content-Type'
        }
    });
};

// Cloudflare Pages Function Handler
export async function onRequest(context) {
    const { request, env } = context;

    // Handle CORS preflight
    if (request.method === 'OPTIONS') {
        return new Response(null, {
            status: 200,
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
                'Access-Control-Allow-Headers': 'Content-Type'
            }
        });
    }

    if (request.method !== 'POST') {
        return jsonResponse({ error: 'Method not allowed' }, 405);
    }

    try {
        const body = await request.json();
        const { message, stockData } = body;

        if (!message) {
            return jsonResponse({ error: 'Message required' }, 400);
        }

        // Get Groq API key from environment
        const GROQ_API_KEY = env.GROQ_API_KEY;

        if (!GROQ_API_KEY) {
            // Fallback response if no API key
            return jsonResponse({
                message: `Thanks for your question about "${message.substring(0, 50)}...". I'm Hero Ai, your financial assistant! 📈\n\nI'm currently in demo mode. For full AI-powered responses, please configure the GROQ_API_KEY environment variable.\n\n💡 **Tip**: Always do your own research before making investment decisions.`,
                model: 'demo'
            });
        }

        // Build context with stock data if available
        let context = '';
        if (stockData) {
            context = `\n\nREAL-TIME STOCK DATA:\n${JSON.stringify(stockData, null, 2)}`;
        }

        // Call Groq API
        const groqResponse = await fetch('https://api.groq.com/openai/v1/chat/completions', {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${GROQ_API_KEY}`,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                model: 'llama-3.3-70b-versatile',
                messages: [
                    { role: 'system', content: SYSTEM_PROMPT + context },
                    { role: 'user', content: message }
                ],
                temperature: 0.7,
                max_tokens: 500
            })
        });

        if (!groqResponse.ok) {
            const error = await groqResponse.text();
            console.error('Groq API error:', error);

            return jsonResponse({
                message: `I understand you're asking about "${message.substring(0, 30)}...". Let me provide some general guidance.\n\n📈 For Saudi stocks, always check the TASI index for overall market direction.\n\n⚠️ Remember: Past performance doesn't guarantee future results. Consider your risk tolerance before investing.`,
                model: 'fallback'
            });
        }

        const data = await groqResponse.json();
        const aiMessage = data.choices?.[0]?.message?.content || 'I apologize, I could not generate a response.';

        return jsonResponse({
            message: aiMessage,
            model: 'llama-3.3-70b',
            symbols: extractStockSymbols(message)
        });

    } catch (error) {
        console.error('Chatbot error:', error.message);
        return jsonResponse({
            message: `I'm experiencing some technical difficulties. Please try again in a moment.\n\n💡 In the meantime, you can check the News Feed or Market Summary for the latest updates.`,
            error: error.message
        }, 200);
    }
}
